"use client";

import { useState } from 'react';

export default function Navbar() {
  const [open, setOpen] = useState(false);

  return (
    <header className="bg-white border-b">
      <div className="flex items-center justify-between px-6 py-3">
        <div className="flex items-center gap-4">
          <button className="md:hidden p-2">☰</button>
          <div className="font-semibold">All Organizations ▾</div>
        </div>

        <div className="flex items-center gap-4">
          <div className="text-sm text-gray-600">
            Hello, <strong>Animesh</strong>
          </div>

          <div className="relative">
            <button
              onClick={() => setOpen(!open)}
              className="w-10 h-10 rounded-full bg-gray-200"
            />

            {open && (
              <div className="absolute right-0 mt-2 w-40 bg-white shadow rounded">
                <button className="block w-full text-left px-4 py-2">
                  User Profile
                </button>
                <button className="block w-full text-left px-4 py-2 text-red-600">
                  Logout
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </header>
  );
}
