import Link from 'next/link';

export default function Sidebar() {
  const items = [
    'Dashboard',
    'Employees',
    'Manage Shifts',
    'Leaves & Holidays',
    'Attendance',
    'Payroll',
    'Loans & Advances',
    'Reports',
    'Configuration',
  ];

  return (
    <aside className="w-64 bg-white h-screen border-r sticky top-0">
      <div className="p-6">
        <h1 className="text-2xl font-bold mb-6">HRMS</h1>

        <nav className="flex flex-col gap-2">
          {items.map((it) => (
            <Link key={it} href={`/${it.toLowerCase().replace(/\s+/g, '-')}`} className="block px-3 py-2 rounded hover:bg-blue-50">
              {it}
            </Link>
          ))}
        </nav>
      </div>
    </aside>
  );
}